"use client";
import { useGetUserInfo } from "@/app/lib/dashboardServices/dashboardQueries";
import Image from "next/image";
import React from "react";
import userAvatar from "../../../public/resourses/userAvatar.svg"
import {  Eye, MapPin } from "lucide-react";

const PublicStore = () => {
  // get The information from API
  const getUserInfo = useGetP();
  const { data } = getUserInfo;

  return (
    <div>
      {/* Top Section */}
      <div className="w-full flex px-20">
        {/* Right Section */}
        <div className="sm:w-[50%]  w-full bg-red-400 flex ">
          <div className="ml-20">
            <Image src={userAvatar} alt="profile image" className="w-10 h-10"  width={1} height={1} />
          </div>
          <div>
            <h2>{data?.username}</h2>
            <div>Stars</div>
            <div>{data?.rating_count}</div>
          </div>

        </div>

        {/* Left Section */}
        <div className="sm:w-[50%]  w-full bg-cblue">
          <div className="flex">
            <MapPin />
            <span>{data?.city}</span>
          </div>
          <div className="flex">
            <Eye />
            <span className="mx-2">{data?.total_views}</span>مشاهدة
          </div>
          <div  className="flex gap-2 px-2">
          <button
            type="submit"
            className="w-full py-3 bg-cgreen text-white text-6 font-semibold rounded-md hover:bg-chgreen disabled:opacity-60 transition"
          >
            تقييم المتجر
          </button>
          <button
            type="submit"
            className="w-full py-3 bg-cgreen text-white text-6 font-semibold rounded-md hover:bg-chgreen disabled:opacity-60 transition"
          >
            ابلاغ
          </button>
        </div>
        </div>
      </div>
      {/* bottom section */}
      <div></div>
    </div>
  );
};

export default PublicStore;
